body {
  background-color: #22272e;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

#game-container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

#markdown-container {
  justify-content: center;
  align-items: center;
  width: 70%;
  padding-top: 20px;
  display: none;
  position: relative;
}

#wiki-list, #page-container {
  width: 100%;
  max-height: calc(100vh - 50px);
  padding-top: 40px;
  overflow-y: auto;
}

#page-container {
  color: #adbac7;
  background-color: #22272e;
  flex-direction: column;
  font-family: Arial, sans-serif;
  line-height: 1.5;
  display: none;
  position: relative;
}

.button {
  box-sizing: border-box;
  color: #0f1111;
  cursor: pointer;
  text-align: center;
  touch-action: manipulation;
  vertical-align: middle;
  background-color: #fff;
  border: 1px solid #d5d9d9;
  border-radius: 8px;
  width: 150px;
  margin-top: 10px;
  margin-left: 10px;
  margin-right: 10px;
  padding: 0 10px 0 11px;
  font-size: 13px;
  line-height: 29px;
  text-decoration: none;
  display: inline-block;
}

#back-btn, #page-back-btn {
  position: absolute;
  top: 10px;
  left: 0;
}

#wiki-list li {
  margin-bottom: 10px;
  list-style-type: none;
}

#wiki-list li a {
  color: #adbac7;
  border: 1px solid #adbac7;
  border-radius: 5px;
  padding: 10px 20px;
  text-decoration: none;
  transition: background-color .3s;
  display: inline-block;
}

#wiki-list li a:hover {
  color: #22272e;
  background-color: #adbac7;
}

#wiki-list li a:visited {
  color: #adbac7;
}

#page-container h1, #page-container h2, #page-container h3, #page-container h4, #page-container h5, #page-container h6 {
  color: #cdd9e5;
  margin-top: 10px;
  margin-bottom: 10px;
  font-weight: 600;
  line-height: 1;
}

#page-container h1 {
  font-size: 2em;
}

#page-container h2 {
  font-size: 1.5em;
}

#page-container h3 {
  font-size: 1.25em;
}

#page-container h4 {
  font-size: 1em;
}

#page-container h5 {
  font-size: .875em;
}

#page-container h6 {
  color: #6a737d;
  font-size: .85em;
}

#page-container p {
  margin-top: 16px;
}

#page-container a {
  color: #539bf5;
  text-decoration: none;
}

#page-container a:hover {
  text-decoration: underline;
}

#page-container img {
  max-width: 100%;
}

#page-container pre {
  background-color: #444c56;
  border-radius: 3px;
  padding: 16px;
  line-height: 1.45;
  overflow: auto;
}

#page-container code {
  color: #cdd9e5;
  background-color: #1b1f230d;
  border-radius: 3px;
  margin: 0;
  padding: .2em .4em;
  font-size: 85%;
}

#page-container pre > code {
  word-break: break-all;
  white-space: pre;
  background: none;
  border: 0;
  margin: 0;
  padding: 0;
  font-size: 100%;
}

#pdf-viewer-container {
  flex-direction: column;
  width: 100%;
  height: 100%;
  display: none;
}

#pdf-controls {
  position: absolute;
  top: 5%;
  left: 0;
}

#pdf-container {
  flex-grow: 1;
}

#pdf-object {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

